import React from 'react';
import { Component } from 'react';
import './Clock.css';

class Clock extends Component {
  state = {
    hours: new Date().getHours(),
    minutes: new Date().getMinutes(),
    seconds:  new Date().getSeconds(),
  }

  componentWillMount() {
    this.timerID = setInterval( () => {
      this.setState({
        hours: new Date().getHours(),
        minutes: new Date().getMinutes(),
        seconds:  new Date().getSeconds(),
      })
    },1000)
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  render() {
    const { seconds, hours, minutes } = this.state;
    const secondsArrow = Math.round(seconds * 6);
    const minutesArrow = Math.round(minutes * 6 + 6 * seconds / 60);
    const hoursArrow = Math.round(hours % 12 * 30 + 6 * minutes / 60);

    return (
      <div className="clock">
        <div
          className="clock__seconds"
          style={{ transform: 'rotate(' + secondsArrow + 'deg)' }}
          >
        </div>
        <div
          className="clock__minutes"
          style={{ transform: 'rotate(' + minutesArrow + 'deg)' }}
          >
        </div>
        <div
          className="clock__hours"
          style={{ transform: 'rotate(' + hoursArrow + 'deg)' }}
          >
        </div>
      </div>
    )
  }
}

export { Clock };
